import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true);
const MARGIN_TOP_BACK = mu.marginTopBack(false);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

import Team from '../../components/team';

const initialState = {
	isLoading: false,
};

@observer
class Teams extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	async loadTeams() {
		this.setState({ isLoading: true });
		const { teamStore, userStore, messageStore } = this.props.stores;
		await teamStore.load(undefined, false, { addData: ['workoutSummary', 'musherSummary'] });
		await userStore.getNotifications();
        await messageStore.getUnReadMessages();
		this.setState({ isLoading: false });
	}

	componentDidMount() {
		this.loadTeams();
	}

	render() {
        const { scrolledDown } = this.props;
		const { isLoading } = this.state;
		const { teamStore, userStore, appState } = this.props.stores;
		const { teams } = teamStore;
		const { user } = userStore;
		const { darkmode } = appState;

		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
				<div class='row fixed-top' style={`margin-top: ${scrolledDown > 0 ? 0 : SUB_MENU_MARGIN_TOP}; background-color: ${darkmode ? '#191d21' : '#f8f9fa'}; transition: all 0.3s ease-in-out;`}>
					<div class='col text-center border-bottom border-primary subtopmenu'>
						<a href='/teams/' class='text-primary stretched-link'><nobr><i class='fas fa-users' /> <Text id='teams.your-teams'>Your teams</Text></nobr></a>
					</div>
					<div class='col text-center subtopmenu'>
						<a href='/teams/connect' class='text-secondary stretched-link'><nobr><i class='fas fa-users' /> <Text id='teams.public-teams'>Public teams</Text></nobr></a>
					</div>
				</div>
				<div class='row'>
					<div class='col-12'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
							<h5><Text id='teams.all-teams' fields={{ total: teams.length }}>Teamene dine - {teams.length} stk</Text></h5>
						</div>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 clearfix mb-2'>
							<a class='btn btn-sm btn-primary float-right'
								href='/teams/edit/new'
							>
								<i class='fas fa-plus' /> <Text id='teams.add-new'>Legg til team</Text>
							</a>
						</div>
						{teams && teams.map(team => (
							<Team stores={this.props.stores} team={team} />
						))}
					</div>
				</div>
			</div>
		);
	}
}

export default Teams;
