import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import Speed from '../../components/data/speed';
import Elevation from '../../components/data/elevation';
import Weight from '../../components/data/weight';
import Distance from '../../components/data/distance';
import Temperature from '../../components/data/temperature';

import mu from '../../lib/musher-util';

@observer
class Team extends Component {
    requestFollowTeam = (e) => {
        e.preventDefault();
        e.stopPropagation();
        const { teamStore, userStore } = this.props.stores;
        const { user } = userStore;
        const teamId = parseInt(e.target.parentElement.dataset.team || e.target.dataset.team, 10);
        teamStore.followRequest(teamId, user);
        this.setState({ requestSent: true });
    }

    getButton = (text = '') => {
        const { requestSent } = this.state;
        const { userStore } = this.props.stores;
        const { user } = userStore;
        const { team, skipHome } = this.props;
        const isYourTeam = team.members && team.members.indexOf(user.id) > -1;
        const isFollowing = team.followers && team.followers.indexOf(user.id) > -1;
        const isBlocked = team.blockedFollowers && team.blockedFollowers.indexOf(user.id) > -1;
        const hasRequestedFollow = requestSent || team.followRequests && team.followRequests.findIndex(e => e.id === user.id) > -1;

        if (isYourTeam) {
            if (skipHome) {
                return '';
            }
            return (
                <i class='fas fa-home' />
            );
        }
        if (isFollowing) {
            return (
                <button
                    type='button'
                    class={`btn btn-sm btn-outline-secondary float-right`}
                    onClick={this.unFollowTeam}
                    disabled='true'
                    data-team={team.id}
                >
                    <i class='fas fa-link mr-2' />
                    <Text id='teams.connect.is-following'>Følger</Text> {text}
                </button>
            );
        }
        if (isBlocked) {
            return '';
            // return (
            //     <button
            //         class={`btn btn-block btn-outline-secondary float-right`}
            //         disabled='true'
            //     >
            //         <i class='fas fa-hand-paper mr-2' />
            //         <Text id='teams.connect.is-blocked'>Blokkert</Text> {text}
            //     </button>
            // );
        }
        if (hasRequestedFollow) {
            return (
                <button
                    type='button'
                    class={`btn btn-sm btn-outline-secondary float-right`}
                    disabled='true'
                >
                    <i class='fas fa-hourglass-half mr-2' />
                    <Text id='teams.connect.has-requested'>Venter på godkjenning</Text> {text}
                </button>
            );
        }
        if (team.public) {
            return (
                <button
                    type='button'
                    class={`btn btn-sm btn-outline-success float-right`}
                    onClick={this.requestFollowTeam}
                    data-team={team.id}
                >
                    <i class='far fa-hand-point-right mr-2' />
                    <Text id='teams.connect.follow'>Følg</Text> {text}
                </button>
            );
        }
    }

    render() {
        const { team, showButton, publicUsers = [], showLocation, showMembers } = this.props;
        const { teamStore, userStore } = this.props.stores;

        const currentMonth = new Date().getMonth() + 1;
        const currentYear = new Date().getFullYear();
		const { month = currentMonth, year = currentYear } = this.props;
		const seasonStart = parseInt(month < 8 ? year - 1 : year, 10);

        const workoutSummary = teamStore.findWorkoutSummary(team.id, seasonStart) || {};
        const { user } = userStore;
        const { language = 'en' } = user;
        const teamAmbition = userStore.findTeamAmbition({ ambition: team.ambitions, language });
        const teamType = userStore.findTeamType({ type: team.type, language });

        const teamUser = publicUsers.find(e => e.id === team.user);
        const teamUserDisplayName = mu.displayName(teamUser);
        const image = mu.getImage({ user: teamUser, team, priority: 'user' });

        const { url = `/teams/${team.id}` } = this.props;

        return (
            <div class='w-100 mb-3'>
                <div class='row'>
                    <div class='col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 d-flex mb-0 position-relative'>
                        <div class='position-relative d-flex flex-grow-1'>
                            {/* {JSON.stringify(team, null, 4)} */}
                            <div class={`text-center border rounded-circle imageRounded`} style={image ? `background-image: url("${image}"); background-size: cover;` : ''}>
                                {!image && <i class='fas fa-users text-muted mt-3' style='font-size: 40px;' />}
                            </div>
                            <div class='flex-grow-1 pl-3' style='line-height: 1.2em;'>
                                <a class='stretched-link' href={url}>
                                    <h5 class='mb-1' style='line-height: 1.0em;'>{team.name}</h5>
                                </a>
                                {showLocation && <div>
                                    {(team.place || team.country) && <span class='mr-3'>
                                        <i class='fas fa-map-marker-alt' />  {team.place} {team.place ? `, ${team.country}` : team.country}
                                    </span>}
                                </div>}
                                {showMembers && <div>
                                    {teamUser && <span class='mr-2'>
                                        <i class='fas fa-user' /> {teamUserDisplayName}
                                    </span>}

                                    {/* {team.members && team.members.length > 1 && team.members.slice(1, 10).map(member => {
                                        const u = publicUsers.find(e => e.id === member);
                                        const teamUserDisplayName = mu.displayName(u);
                                        return (<span class='badge badge-secondary font-weight-light mr-2'>
                                            <i class='fas fa-user' /> {teamUserDisplayName}
                                        </span>);
                                    })} */}
                                </div>}
                                <div>
                                    {teamType && <span class='badge badge-pill badge-secondary'>{teamType}</span>}
                                    {teamAmbition && <span class='badge badge-pill badge-secondary ml-2'>{teamAmbition}</span>}
                                </div>
                                <small class='text-muted'>
                                    {team && team.dogs && <span class=''><i class='fas fa-dog' /> {team.dogs.length}</span>} 
                                    {team && team.members && <span class='ml-2'><i class='fas fa-user' /> {team.members.length}</span>}

                                    {workoutSummary && workoutSummary.distanceKm > 0 && <span class='ml-2'><i class='fas fa-road' /> <Distance stores={this.props.stores} value={workoutSummary.distanceKm} /></span>}
                                    {workoutSummary && workoutSummary.elevation > 0 && <span class='ml-2'><i class='fas fa-mountain' /> <Elevation stores={this.props.stores} value={workoutSummary.elevation} /></span>}
                                    {workoutSummary && workoutSummary.duration > 0 && <span class='ml-2'><i class='fas fa-clock' /> {util.secToHms(workoutSummary.duration)}</span>}
                                    {workoutSummary && workoutSummary.speedAvg > 0 && <span class='ml-2'><i class='fas fa-tachometer-alt' /> <Speed stores={this.props.stores} value={workoutSummary.speedAvg} /></span>}
                                </small>
                            </div>
                        </div>
                        {showButton && <div>
                            {this.getButton()}
                        </div>}
                    </div>
                </div>
            </div>
        );
    }
}

export default Team;
